<template>
  <div class="post flex-column" v-loading="loading">
    <i class="page-control fas fa-long-arrow-alt-left" @click="$router.go(-1)">
      返回
    </i>
    <h1 class="post__header">
      {{ post.title }}
    </h1>
    <ul class="flex-1">
      <li
      v-for="reply in replys"
      :key="reply._id"
      >
      <reply 
      :reply="reply"
      />
      </li>
    </ul>
    <pager 
    class="mt-1"
    :len="pager.limit"
    :total="pager.total"
    :active="pager.page"
    @click="toPage"
    />
    <form class="mt-5 post-reply-form pure-form pure-form-stacked" @submit.prevent="toReply">
      <textarea 
      class="w-100" name="post-reply-input" id="post-reply-input" cols="30" rows="10" required
      :disabled="reply.flag"
      ></textarea>
      <button class="pure-button" :disabled="reply.flag">Post</button>
    </form>
  </div>
</template>

<script>
import Reply from './components/reply.vue';

export default {
  components: {
    Reply,
  },
  props: {
    title: {
      type: String,
      required: true,
    }
  },
  computed: {
    replys() {
      const start = (this.pager.page - 1) * this.pager.limit;
      const end = start + this.pager.limit;
      return this.post.replys.slice(start, end);
    }
  },
  data() {
    return {
      loading: false,
      post: {
        title: '',
        replys: [],
      },

      reply: {
        flag: false,
      },
      pager: {
        page: 1,
        limit: 10,
        lastPage: 0,
        total: 0,
      }
    }
  },
  mounted() {
    this.getData();
  },
  methods: {
    toPage(page) {
      this.pager.page = page;
      this.$nextTick(() => {
        window.scrollTo(0, 0);
      })
    },
    getData() {
      this.loading = true;
      this.$axios.get(`${this.$url.posts}/${this.title}`)
      .then((resp) => {
        this.post = resp.data;
        console.log(this.post.replys.length);
        this.pager.total = resp.data.replys.length;
        this.pager.lastPage = Math.floor(this.pager.total / this.pager.limit);
        if (this.pager.total % this.pager.limit !== 0) {
          this.pager.lastPage += 1;
        }
        this.loading = false;
      })
    },
    toReply() {
      if (this.reply.flag) {
        return;
      }
      const el = document.getElementById('post-reply-input');
      if (el) {
        const value = el.value.trim();
        if (value) {
          const user = window.$user;
          const reply = {
            title: this.title,
            username: user.username,
            avatar: user.avatar,
            content: value,
          }
          this.reply.flag = true;
          this.$axios.post(`${this.$url.posts}/${this.title}/replys`, reply)
          .then((resp) => {
            el.value = '';
            this.post.replys.push(resp.data);
            this.pager.total += 1;
            this.pager.lastPage = Math.floor(this.pager.total / this.pager.limit);
            if (this.pager.total % this.pager.limit !== 0) {
              this.pager.lastPage += 1;
            }
            this.$nextTick(() => {
              window.scrollTo(0, 0);
            })
            this.pager.page = this.pager.lastPage;
            this.reply.flag = false;
          })
        }
      }
    },
  }
}
</script>

<style lang="scss" scoped>
.page-control {
  color: lighten($primary, 20%);
  font-size: $font-sm;
  transition: all 0.3s;

  &:hover {
    cursor: pointer;
    color: $primary;
  }
}
.post {
  margin: 2em;
}
</style>
